<ul class="projects"><li class="projects__item" v-for="item in projects" :key="item.id"><div class="projects__imgWrapper"><img class="projects__img" :src="item.img" alt="alt" loading="lazy" v-motion-slide-visible-top :enter="{ opacity: 1, y: 0, scale: 1 }" :variants="{ custom: { scale: 1 } }" :delay="100"></div><div class="projects__content" v-motion-pop-visible :enter="{ opacity: 1, y: 0, scale: 1 }" :variants="{ custom: { scale: 2 } }" :hovered="{ scale: 1 }" :delay="100"><span class="projects__geo">{{item.geo}}</span><h3 class="projects__title">{{item.title}}</h3><span class="projects__url">{{item.more}}</span></div></li></ul>